.container{max-width:400px;padding:20px;margin:0 auto;font-family:Arial,sans-serif;color:#878787;position:relative;display:flex;flex-direction:column;align-items:center}.header{display:flex;justify-content:space-between;align-items:center;width:100%}.backButton{height:40px;width:40px;background:url("../../images/icons/back_button.svg") center no-repeat;background-size:100%;border:none;border-radius:50%;cursor:pointer;transition:.5s}.backButton:hover{transform:rotate(-10deg)}.city{font-size:32px;font-weight:bold;text-align:center}.time{margin-top:8px;font-size:20px;text-align:center}.weather{padding:20px 0;display:flex;align-items:center}.description{font-size:24px}.temperature{font-size:32px;color:#000;margin-left:16px;position:relative}.temperature:after{font-size:16px;content:"°C";position:absolute;top:-5px;right:-20px}.weatherIcon{margin-left:24px}